@import '../../styles/styles.scss';

.list {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    grid-column-gap: 24px;

    @include media-min(tablet) {
        grid-template-columns: repeat(2, 1fr);
        grid-row-gap: 32px;
    }

    @include media-min(desktop) {
        grid-template-columns: repeat(4, 1fr);
    }
}